import React, { useState } from 'react';
import MovieCard from './MovieCard';

function Home() {
  const [string, setString] = useState("");
  const [currentMovie, setCurrentMovie] = useState([]);

  const handleInput= (e)=>{
    e.preventDefault()
    setString(e.target.value)
    
    fetch(`https://api.themoviedb.org/3/search/movie?api_key=a3eca5539e3715256e80a664443dbf05&language=en-US&query=${e.target.value}&page=1&include_adult=true`)
    .then( data => data.json())
    .then(data => {
      if(data.error){
        setCurrentMovie([]);
      }else{
        setCurrentMovie(data.results)
      }
      console.log(data);
    });
  }

  return (
    <div className="home">
      <input 
        type="text" 
        placeholder="search movie"
        onChange={handleInput}
        value={string}
      />
        {currentMovie.length > 0 && (
          <div >
            {currentMovie.map(movie =>(
              <MovieCard key={movie.id} movie={movie}/>
            ))}
          </div>
        )}  
    </div>
  )
}

export default Home
